import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { Category } from 'src/Table/table';
import { AppError } from '../common/app-error';
import { BadInput } from '../common/bad-input';
import { NotFoundError } from '../common/not-found-error';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {
  private categoryCollectionList: AngularFirestoreCollection<any>;
  private categoryList: Observable<any[]>;
  constructor(private db: AngularFirestore) {
    this.categoryCollectionList = db.collection<Category>('Category');
  }
  create(categoryObj) { 
    return this.categoryCollectionList.add(categoryObj)
  }
  getAllCategory() {
    this.categoryList = this.categoryCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.categoryList;
  }
  getCategory(id) {
    return this.categoryCollectionList.doc<any>(id).valueChanges();
  }
  updateCategory(categoryObj: Category, id: string) {
    return this.categoryCollectionList.doc(id).update(categoryObj)
    .catch(this.handleError);
  }
  removeCategory(id) {
    return this.categoryCollectionList.doc(id).delete()
    .catch(this.handleError);
  }
  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInput(error.json()));
  
    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    
    return Observable.throw(new AppError(error));
  }
}
