import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { Vehicle } from 'src/Table/table';
import { AppError } from '../common/app-error';
import { BadInput } from '../common/bad-input';
import { NotFoundError } from '../common/not-found-error';

@Injectable({
  providedIn: 'root'
})
export class VehicleService {
  private vehicleCollectionList: AngularFirestoreCollection<any>;
  private vehicleList: Observable<any[]>;
  constructor(private db: AngularFirestore) {
    this.vehicleCollectionList = db.collection<Vehicle>('Vehicle');
  }
  create(vehicleObj) { 
    return this.vehicleCollectionList.add(vehicleObj)
  }
  getAllVehicle() {
    this.vehicleList = this.vehicleCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.vehicleList;
  }
  getVehicle(id) {
    return this.vehicleCollectionList.doc<any>(id).valueChanges();
  }
  updateVehicle(vehicleObj: Vehicle, id: string) {
    return this.vehicleCollectionList.doc(id).update(vehicleObj)
    .catch(this.handleError);
  }
  removeVehicle(id) {
    return this.vehicleCollectionList.doc(id).delete()
    .catch(this.handleError);
  }
  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInput(error.json()));
  
    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    
    return Observable.throw(new AppError(error));
  }
}
