import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { Driver } from 'src/Table/table';
import { AppError } from '../common/app-error';
import { BadInput } from '../common/bad-input';
import { NotFoundError } from '../common/not-found-error';

@Injectable({
  providedIn: 'root'
})
export class DriverService {
  private driverCollectionList: AngularFirestoreCollection<any>;
  private driverList: Observable<any[]>;
  constructor(private db: AngularFirestore) {
    this.driverCollectionList = db.collection<Driver>('Driver');
  }
  create(driverObj) { 
    return this.driverCollectionList.add(driverObj)
  }
  getAllDriver() {
    this.driverList = this.driverCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.driverList;
  }
  getDriver(id) {
    return this.driverCollectionList.doc<any>(id).valueChanges();
  }
  updateDriver(driverObj: Driver, id: string) {
    return this.driverCollectionList.doc(id).update(driverObj)
    .catch(this.handleError);
  }
  removeDriver(id) {
    return this.driverCollectionList.doc(id).delete()
    .catch(this.handleError);
  }
  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInput(error.json()));
  
    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    
    return Observable.throw(new AppError(error));
  }
}
