import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { Account } from 'src/Table/table';
import { AppError } from '../common/app-error';
import { BadInput } from '../common/bad-input';
import { NotFoundError } from '../common/not-found-error';

@Injectable({
  providedIn: 'root'
})
export class AccountService {
  private accountCollectionList: AngularFirestoreCollection<any>;
  private accountList: Observable<Account[]>;
  constructor(private db: AngularFirestore) {
    this.accountCollectionList = db.collection<Account>('Account');
  }
  create(accountObj) { 
    return this.accountCollectionList.add(accountObj)
  }
  getAllAccount() {
    this.accountList = this.accountCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.accountList;
  }
  getAccountById(id){
    const accountObj = this.db.collection('Account', ref => ref.where('id', '==', id)).snapshotChanges();
    this.accountList = accountObj.pipe(
      map(changes => changes.map(a => {
        const data = a.payload.doc.data() as Account;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    );
    return this.accountList;
  }
  getAccount(id) {
    return this.accountCollectionList.doc<any>(id).valueChanges();
  }
  updateAccount(accountObj: Account, id: string) {
    return this.accountCollectionList.doc(id).update(accountObj)
    .catch(this.handleError);
  }
  removeAccount(id) {
    return this.accountCollectionList.doc(id).delete()
    .catch(this.handleError);
  }
  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInput(error.json()));
  
    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    
    return Observable.throw(new AppError(error));
  }
}
